
#borders {
  color: #F0F2FA;
  font-size: .9rem;
}
#borders .horizontal-border {
    height: 60px;
    width: 100vw;
    position: fixed;
    left: 0;
    z-index: 99;
    background: #3C3D4C;
}
#borders .vertical-border {
    height: 100vh;
    width: 60px;
    position: fixed;
    top: 0;
    z-index: 99;
    background: #3C3D4C;
}
#borders .border-left {
    left: 0;
}
#borders .border-left .logo {
      position: relative;
      z-index: 99999;
      -webkit-transform: rotate(-90deg);
              transform: rotate(-90deg);
      margin-top: 83px;
}
#borders .border-left .logo:hover {
        cursor: pointer;
}
#borders .border-left nav {
      -webkit-transform: rotate(-90deg);
              transform: rotate(-90deg);
      position: absolute;
      left: 30px;
      bottom: 10px;
      width: 100vw;
      -webkit-transform-origin: 0;
              transform-origin: 0;
      z-index: 999;
      text-align: left;
}
#borders .border-left nav ul {
        margin: 0;
}
#borders .border-left nav ul li {
          position: relative;
          display: inline-block;
}
#borders .border-left nav ul li:after {
            content: '';
            width: 0;
            height: 2px;
            background: #FFFFFF;
            position: absolute;
            left: 50%;
            -webkit-transform: translateX(-50%);
                    transform: translateX(-50%);
            bottom: -4px;
            -webkit-transition: width ease .4s;
            transition: width ease .4s;
}
#borders .border-left nav ul li:hover {
            cursor: pointer;
}
#borders .border-left nav ul li:hover:after {
            width: 80%;
}
#borders .border-left nav ul li + li {
          margin-left: 30px;
}
#borders .border-right {
    right: 0;
}
#borders .border-top {
    top: 0;
}
#borders .border-bottom {
    bottom: 0;
    line-height: 60px;
}



