
#intro {
  height: 100vh;
  width: 100vw;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: #F0F2FA;
}
#intro #borders {
    position: absolute;
    height: 100vh;
    width: 100vw;
    display: none;
    top: 0;
    left: 0;
}
#intro #intro-wrapper .center {
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
}
#intro #intro-wrapper h1 {
    display: inline-block;
}
#intro #intro-wrapper img {
    display: inline-block;
    height: 20px;
    margin-left: 20px;
}
#intro #intro-wrapper #scrollto {
    font-size: .8rem;
    position: absolute;
    bottom: 50px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    text-align: center;
}
#intro #intro-wrapper #scrollto a {
      text-decoration: none;
      color: #3C3D4C;
}
#intro #intro-wrapper #bar {
    height: 40px;
    width: 4px;
    background: #3C3D4C;
    position: absolute;
    bottom: 0;
    display: block;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}



