
#project {
  min-height: 100vh;
  background: #FFFFFF;
  font-size: .9rem;
}
#project #project-wrapper {
    padding: 60px;
}
#project #header #banner {
    position: relative;
    width: 100%;
    height: 500px;
    margin: 0 auto;
    background: center center no-repeat;
    background-size: cover;
}
#project #header #banner-mask {
    position: absolute;
    top: 60px;
    left: 60px;
    width: calc(100% - 2 * 60px);
    height: 500px;
    background: #FFFFFF;
    opacity: .5;
    overflow: hidden;
}
#project #header .title {
    text-transform: uppercase;
    font-size: 4rem;
    position: relative;
    top: -30px;
    margin: 0;
}
#project #details {
    padding: 0 40px;
}
#project #details ul {
      margin: 0;
      padding: 0;
}
#project #details ul li {
        display: block;
        line-height: 1.4rem;
}
#project #details .title {
      margin-bottom: 10px;
      font-weight: bold;
}
#project #details .block {
      display: inline-block;
      vertical-align: top;
      min-width: 200px;
      margin-top: 40px;
}
#project #container {
    margin: 20px auto;
    padding: 60px 0;
    max-width: 800px;
}
#project #container .text {
      padding: 0 15px;
      text-align: left;
      line-height: 1.5rem;
}
#project #container .video {
      margin: 40px auto;
}
#project #container .video iframe {
        width: 100%;
        height: 450px;
}
#project #container #gallery {
      margin: 40px auto;
}
#project #container #gallery img {
        width: 100%;
        height: auto;
        margin: 10px auto;
}
@media all and (max-width: 680px) {
#project #header .title {
    font-size: 10vw;
}
#project #container .video iframe {
    height: 360px;
}
}
@media all and (max-width: 520px) {
#project #container .video iframe {
    height: 300px;
}
}
@media all and (max-width: 460px) {
#project #container .video iframe {
    height: 220px;
}
}



