
#about {
  min-height: 100vh;
  background: #FFFFFF;
  font-size: .9rem;
}
#about #about-wrapper {
    padding: 60px;
}
#about #header #banner {
    position: relative;
    width: 100%;
    height: 500px;
    margin: 0 auto;
    background: center center no-repeat;
    background-size: cover;
}
#about #header #banner-mask {
    position: absolute;
    top: 60px;
    left: 60px;
    width: calc(100% - 2 * 60px);
    height: 500px;
    background: #FFFFFF;
    opacity: .3;
    overflow: hidden;
}
#about #header .title {
    text-transform: uppercase;
    font-size: 4rem;
    position: relative;
    top: -30px;
    margin: 0;
}
#about #container {
    margin: 0 auto;
    padding: 0 20px 60px 20px;
    max-width: 500px;
    line-height: 1.4rem;
    font-size: 1rem;
    text-align: left;
}
#about #container #social ul {
      margin: 40px 0;
      padding: 0;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
}
#about #container #social ul li {
        display: inline-block;
}
#about #container #social ul li a {
          color: #3c3d4c;
}
#about #container #thanks a {
      color: #3c3d4c;
}



