
#home {
  min-height: 100vh;
  background: #3C3D4C;
  overflow-x: hidden;
  padding: 0 60px;
}
#home #mask {
    position: fixed;
    top: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    height: 100vh;
    width: 0;
    background: #F0F2FA;
    z-index: 10;
}
#home #projects {
    visibility: hidden;
}
#home #projects ul {
      padding: 60px 0 60px 0;
      margin: 0 auto;
      width: 100%;
}
#home #projects ul li {
        position: relative;
        margin: 0 auto;
        display: block;
        width: 100%;
        height: 500px;
        background: #FFFFFF center center no-repeat;
        background-size: cover;
        -webkit-transition: .25s filter ease;
        transition: .25s filter ease;
        -webkit-filter: grayscale(100%);
                filter: grayscale(100%);
}
#home #projects ul li:hover {
          -webkit-filter: grayscale(0%);
                  filter: grayscale(0%);
          cursor: pointer;
}
#home #projects ul li .wrapper {
          position: absolute;
          bottom: 100px;
          left: 100px;
          color: #FFFFFF;
          text-align: left;
          text-shadow: 1px 1px 10px rgba(0, 0, 0, 0.5);
}
#home #projects ul li .wrapper .title {
            font-size: 2.4rem;
            text-transform: uppercase;
}
#home #projects ul li .wrapper .type {
            margin: -4px 0 2px 0;
}
#home #projects ul li .wrapper .date {
            font-size: 1.1rem;
}
#home #projects ul li + li {
        /*margin-top: 20px;*/
}
@media all and (max-width: 570px) {
#home #projects ul li .wrapper {
    bottom: 20px;
    left: 20px;
}
#home #projects ul li .wrapper .title {
      font-size: 8vw;
}
}



